<h1 id="微信开放平台第三方平台" tabindex="-1"><a class="header-anchor" href="#微信开放平台第三方平台" aria-hidden="true">#</a> 微信开放平台第三方平台</h1>
<p>请仔细阅读并理解：<a href="https://developers.weixin.qq.com/doc/oplatform/Third-party_Platforms/2.0/product/Third_party_platform_appid.html" target="_blank" rel="noopener noreferrer">微信官方文档 - 开放平台 - 第三方平台<OutboundLink/></a></p>
<h2 id="实例化" tabindex="-1"><a class="header-anchor" href="#实例化" aria-hidden="true">#</a> 实例化</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token php language-php"><span class="token delimiter important">&lt;?php</span>
<span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>OpenPlatform<span class="token punctuation">\</span>Application</span><span class="token punctuation">;</span>

<span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
  <span class="token string single-quoted-string">'app_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'wx3cf0f39249eb0exx'</span><span class="token punctuation">,</span>
  <span class="token string single-quoted-string">'secret'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'f1c242f4f28f735d4687abb469072axx'</span><span class="token punctuation">,</span>
  <span class="token string single-quoted-string">'token'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'easywechat'</span><span class="token punctuation">,</span>
  <span class="token string single-quoted-string">'aes_key'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'......'</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Application</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</span></code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><h2 id="api" tabindex="-1"><a class="header-anchor" href="#api" aria-hidden="true">#</a> API</h2>
<p>Application 就是一个工厂类，所有的模块都是从 <code>$app</code> 中访问，并且几乎都提供了协议和 setter 可自定义修改。</p>
<h3 id="服务端" tabindex="-1"><a class="header-anchor" href="#服务端" aria-hidden="true">#</a> 服务端</h3>
<p>服务端模块封装了服务端相关的便捷操作，隐藏了部分复杂的细节，基于中间件模式可以更方便的处理消息推送和服务端验证。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getServer</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>📖 更多说明请参阅：<RouterLink to="/6.x/open-platform/server.html">服务端使用文档</RouterLink></p>
<h3 id="api-client" tabindex="-1"><a class="header-anchor" href="#api-client" aria-hidden="true">#</a> API Client</h3>
<p>封装了多种模式的 API 调用类，你可以选择自己喜欢的方式调用开放平台任意 API，默认自动处理了 access_token 相关的逻辑。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getClient</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>📖 更多说明请参阅：<RouterLink to="/docs/%7B%7Bversion%7D%7D/common/client.html">API调用</RouterLink></p>
<h3 id="配置" tabindex="-1"><a class="header-anchor" href="#配置" aria-hidden="true">#</a> 配置</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$config</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getConfig</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>你可以轻松使用 <code>$config-&gt;get($key, $default)</code> 读取配置，或使用 <code>$config-&gt;set($key, $value)</code> 在调用前修改配置项。</p>
<h3 id="componentaccesstoken" tabindex="-1"><a class="header-anchor" href="#componentaccesstoken" aria-hidden="true">#</a> ComponentAccessToken</h3>
<p>access_token 是开放平台 API 调用的必备条件，如果你想获取它的值，你可以通过以下方式拿到当前的 access_token：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$componentAccessToken</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getComponentAccessToken</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$componentAccessToken</span><span class="token operator">-></span><span class="token function">getToken</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// string</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>当然你也可以使用自己的 ComponentAccessToken 类：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$componentAccessToken</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">MyCustomComponentAccessToken</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token function">setComponentAccessToken</span><span class="token punctuation">(</span><span class="token variable">$componentAccessToken</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h3 id="verifyticket" tabindex="-1"><a class="header-anchor" href="#verifyticket" aria-hidden="true">#</a> VerifyTicket</h3>
<p>你可以通过以下方式拿到当前 verify_ticket 类：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$verifyTicket</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getVerfiyTicket</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$verifyTicket</span><span class="token operator">-></span><span class="token function">getTicket</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// strval</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="开放平台账户" tabindex="-1"><a class="header-anchor" href="#开放平台账户" aria-hidden="true">#</a> 开放平台账户</h3>
<p>开放平台账号类，提供一系列 API 获取开放平台的基本信息：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$account</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getAccount</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$account</span><span class="token operator">-></span><span class="token function">getAppId</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$account</span><span class="token operator">-></span><span class="token function">getSecret</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$account</span><span class="token operator">-></span><span class="token function">getToken</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$account</span><span class="token operator">-></span><span class="token function">getAesKey</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h2 id="第三方应用或网站网页授权" tabindex="-1"><a class="header-anchor" href="#第三方应用或网站网页授权" aria-hidden="true">#</a> 第三方应用或网站网页授权</h2>
<blockquote>
<p>{warning} 注意：不是代公众号/小程序授权。</p>
</blockquote>
<p>第三方应用或者网站网页授权的逻辑和公众号的网页授权基本一样：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$oauth</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getOAuth</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>📖 详情请参考：<RouterLink to="/6.x/common/oauth.html">网页授权</RouterLink></p>
<h2 id="使用授权码获取授权信息" tabindex="-1"><a class="header-anchor" href="#使用授权码获取授权信息" aria-hidden="true">#</a> 使用授权码获取授权信息</h2>
<p>在用户在授权页授权流程完成后，授权页会自动跳转进入回调URI，并在URL参数中返回授权码和过期时间，如：(<code>https://easywechat.com/callback?auth_code=xxx&amp;expires_in=600</code>)</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$authorizationCode</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'授权成功时返回给第三方平台的授权码'</span><span class="token punctuation">;</span>

<span class="token variable">$authorization</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getAuthorization</span><span class="token punctuation">(</span><span class="token variable">$authorizationCode</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$authorization</span><span class="token operator">-></span><span class="token function">getAppId</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// authorizer_appid</span>
<span class="token variable">$authorization</span><span class="token operator">-></span><span class="token function">getAccessToken</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// EasyWeChat\OpenPlatform\AuthorizerAccessToken</span>
<span class="token variable">$authorization</span><span class="token operator">-></span><span class="token function">getRefreshToken</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// authorizer_access_token</span>
<span class="token variable">$authorization</span><span class="token operator">-></span><span class="token function">toArray</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$authorization</span><span class="token operator">-></span><span class="token function">toJson</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// {</span>
<span class="token comment">//   "authorization_info": {</span>
<span class="token comment">//     "authorizer_appid": "wxf8b4f85f3a79...",</span>
<span class="token comment">//     "authorizer_access_token": "QXjUqNqfYVH0yBE1iI_7vuN_9gQbpjfK7M...",</span>
<span class="token comment">//     "expires_in": 7200,</span>
<span class="token comment">//     "authorizer_refresh_token": "dTo-YCXPL4llX-u1W1pPpnp8Hgm4wpJt...",</span>
<span class="token comment">//     "func_info": [</span>
<span class="token comment">//       {</span>
<span class="token comment">//         "funcscope_category": {</span>
<span class="token comment">//           "id": 1</span>
<span class="token comment">//         }</span>
<span class="token comment">//       },</span>
<span class="token comment">//       //...</span>
<span class="token comment">//     ]</span>
<span class="token comment">//   }</span>
<span class="token comment">// }</span>

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br></div></div><h2 id="获取-刷新接口调用令牌" tabindex="-1"><a class="header-anchor" href="#获取-刷新接口调用令牌" aria-hidden="true">#</a> 获取/刷新接口调用令牌</h2>
<p>在公众号/小程序接口调用令牌（<code>authorizer_access_token</code>）失效时，可以使用刷新令牌（authorizer_refresh_token）获取新的接口调用令牌。</p>
<blockquote>
<p>{warning} 注意： <code>authorizer_access_token</code> 有效期为 2 小时，开发者需要缓存 <code>authorizer_access_token</code>，避免获取/刷新接口调用令牌的 API 调用触发每日限额。缓存方法可以参考：https://developers.weixin.qq.com/doc/offiaccount/Basic_Information/Get_access_token.html</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$authorizerAppId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'授权方 appid'</span><span class="token punctuation">;</span>
<span class="token variable">$authorizerRefreshToken</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'刷新令牌，上一步得到的 authorizer_refresh_token'</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token function">refreshAuthorizerToken</span><span class="token punctuation">(</span><span class="token variable">$authorizerAppId</span><span class="token punctuation">,</span> <span class="token variable">$authorizerRefreshToken</span><span class="token punctuation">)</span>

<span class="token comment">// {</span>
<span class="token comment">//   "authorizer_access_token": "some-access-token",</span>
<span class="token comment">//   "expires_in": 7200,</span>
<span class="token comment">//   "authorizer_refresh_token": "refresh_token_value"</span>
<span class="token comment">// }</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div><hr>
<h2 id="代替公众号-小程序请求公众平台-api" tabindex="-1"><a class="header-anchor" href="#代替公众号-小程序请求公众平台-api" aria-hidden="true">#</a> 代替公众号/小程序请求公众平台 API</h2>
<p>代替公众号/小程序请求，需要首先拿到 <code>EasyWeChat\OpenPlatform\AuthorizerAccessToken</code>。</p>
<p><strong>获取 AuthorizerAccessToken</strong></p>
<p>第一种方式：开放平台永久授权码换取授权者信息</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$authorizationCode</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'授权成功时返回给第三方平台的授权码'</span><span class="token punctuation">;</span>
<span class="token variable">$authorization</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getAuthorization</span><span class="token punctuation">(</span><span class="token variable">$authorizationCode</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$authorizerAccessToken</span> <span class="token operator">=</span> <span class="token variable">$authorization</span><span class="token operator">-></span><span class="token function">getAccessToken</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>第二种方式：从数据库提取出来的授权码</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$authorizerAccessToken</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">AuthorizerAccessToken</span><span class="token punctuation">(</span><span class="token variable">$authorizerAppId</span><span class="token punctuation">,</span> <span class="token variable">$token</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p><strong>获取公众号实例</strong></p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// $officialAccount 为 EasyWeChat\OfficialAccount\Application 实例</span>
<span class="token variable">$officialAccount</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getOfficialAccount</span><span class="token punctuation">(</span><span class="token variable">$authorizerAccessToken</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 调用公众号接口</span>
<span class="token variable">$users</span> <span class="token operator">=</span> <span class="token variable">$officialAccount</span><span class="token operator">-></span><span class="token function">getClient</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token property">cgiBin</span><span class="token operator">-></span><span class="token property">users</span><span class="token operator">-></span><span class="token property">list</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">toArray</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>📖 更多公众号用法请参考：<RouterLink to="/docs/%7B%7Bversion%7D%7D/official-account/">公众号</RouterLink></p>
<p><strong>获取小程序实例</strong></p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// $miniApp 为 EasyWeChat\MiniApp\Application 实例</span>
<span class="token variable">$miniApp</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getMiniApp</span><span class="token punctuation">(</span><span class="token variable">$authorizerAccessToken</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 调用小程序接口</span>
<span class="token variable">$users</span> <span class="token operator">=</span> <span class="token variable">$miniApp</span><span class="token operator">-></span><span class="token function">getClient</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token property">cgiBin</span><span class="token operator">-></span><span class="token property">users</span><span class="token operator">-></span><span class="token property">list</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">toArray</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>📖 更多小程序用法请参考：<RouterLink to="/docs/%7B%7Bversion%7D%7D/mini-app/">小程序</RouterLink></p>
